@use "./variables";
@use "./mixins";

#root {
    width: 100vw;
    height: 100vh;
    font-family: "Lato", sans-serif;
}

.circular--square {
    border-radius: 50%;
    position: relative;
}

body {
    height: 100vh;
    width: 100vw;
}

input,
select,
textarea {
    color: var(--main-text-color);
    font-size: 16px;
}

.flexbox-row-v-centered {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.flexbox-row-centered {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

* {
    box-sizing: border-box;
}
.button--alarm {
    @extend .flexbox-row-centered;
    border: 2px solid var(--ytd-alarm-color);
    border-radius: var(--ytd-search-height);
    cursor: pointer;
    &:disabled {
        cursor: default;
    }
    height: var(--ytd-search-height);
    width: var(--ytd-search-height);
}

.button {
    border: none;
    background-color: initial;
    text-align: inherit;
    cursor: pointer;
    &:disabled {
        cursor: default;
    }
    &.smaller {
        & svg {
            width: var(--svg-smaller) !important;
            height: var(--svg-smaller) !important;
        }
    }
}

.button--neutral {
    border: none;
    background-color: initial;
    text-align: inherit;
    cursor: pointer;
    &:disabled {
        cursor: default;
    }
    & svg {
        width: var(--svg-width);
    }
}

.button--round {
    border: none;
    border-radius: var(--ytd-search-height);
    cursor: pointer;
    height: var(--ytd-search-height);
    width: var(--ytd-search-height);
    margin: 0;
}
.darkening {
    transition: background-color 0.3s;
    &:hover {
        background-color: var(--yt-spec-touch-response);
    }
}

.button--reverted {
    @extend .flexbox-row-centered;
    @extend .button--round;
    @extend .darkening;
    @include svg--larger;
    background-color: var(--ytd-searchbox-background);

    & svg {
        fill: var(--yt-spec-text-primary);
    }
}

.hidden {
    display: none;
}

.button--left {
    position: absolute;
    top: 40px;
    left: 0;
    @include largeScreen {
        bottom: 0;
        left: 120px;
        // transform: rotateX(90deg);
    }
}

.button--right {
    position: absolute;
    top: 40px;
    right: 0;
    @include largeScreen {
        top: 0;
        left: 120px;
        //transform: rotateX(90deg);
    }
}

.button--upwards {
    @extend .button--right;
    transform: rotateZ(90deg);
}

.button--downwards {
    @extend .button--left;
    transform: rotateZ(90deg);
}

.button--sidebar {
    border: none;
    border-radius: 40px;
    border-radius: var(--ytd-search-height);
    cursor: pointer;
    height: 40px;
    height: var(--ytd-search-height);
    width: 40px;
    width: var(--ytd-search-height);
    margin: 0;
    background-color: var(--ytd-alarm-color);
    &:disabled {
        background-color: var(--yt-spec-touch-response);
    }
    z-index: 1000;
}

.main {
    display: flex;
    flex-direction: row;
    padding-top: 20px;
    @include mobile {
        flex-direction: column;
    }
    :nth-child(2) {
        flex-grow: 1;
    }
}
