/* Absolute Center Spinner */

$hide-position: -45px;

.error {
    @extend .flexbox-row-centered;
    position: fixed;
    z-index: 999;
    height: 2em;
    width: 2em;
    overflow: show;
    margin: auto;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    & > .message {
        position: relative;
        border: 2px solid var(--ytd-alarm-color);
        width: 300px;
        min-width: 300px;
        padding: 10px;
        & > .message__hide-message {
            position: absolute;
            top: $hide-position;
            right: $hide-position;
            & svg {
                width: var(--svg-larger);
                fill: var(--ytd-alarm-color);
            }
        }
        & > .message__general-message {
            text-align: center;
        }
        & > .message__error-message {
        }
    }
}

/* Transparent Overlay */
.error:before {
    content: "";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(rgba(20, 20, 20, 0.1), rgba(0, 0, 0, 0.1));

    background: -webkit-radial-gradient(rgba(20, 20, 20, 0.1), rgba(0, 0, 0, 0.1));
}

