}
.error > .message > .message__hide-message {
  position: absolute;
  top: -45px;
  right: -45px;
}
.error > .message > .message__hide-message svg {
  width: var(--svg-larger);
  fill: var(--ytd-alarm-color);
}
.error > .message > .message__general-message {
  text-align: center;
}
/* Transparent Overlay */
.error:before {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(rgba(20, 20, 20, 0.1), rgba(0, 0, 0, 0.1));
